<template lang="pug">
    el-card(style="width: 600px; margin: 0 auto"  v-loading="loading" )
      div(slot="header") Авторизация
      el-form(:model="loginForm" status-icon :rules="rules" ref="loginForm" label-width="120px" )
          el-form-item(prop="login" label="Логин")
              el-input(v-model="loginForm.login")
          el-form-item(prop="password" label="Пароль")
              el-input(v-model="loginForm.password" type="password" auto-complete="off")
          el-form-item
              el-button(@click="submitForm('loginForm')" type="primary") Вход
</template>

<script>
import { mapActions } from 'vuex'
export default {
  name: 'login',
  data () {
    return {
      loading: false,
      loginForm: {
        login: '',
        password: ''
      },
      rules: {
        login: { required: true, message: 'Введите логин', trigger: 'blur' },
        password: { required: true, message: 'Введите пароль', trigger: 'blur' }
      }
    }
  },
  methods: {
    ...mapActions(['login']),
    submitForm (formName) {
      let that = this
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.loading = true
          this.login(this.loginForm)
            .then(() => {
              that.loading = false
            },
            () => {
              this.loading = false
              this.$message({
                message: 'Неверное имя пользователя или пароль',
                type: 'error'
              })
            })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    }
  }
}
</script>

<style>

</style>



