
export default {
  data () {
    return {
      entityForEdit: null,
      editModal: false,
      rules: []
    }
  },
  methods: {
    deleteEntity (entity) {
      return new Promise()
    },
    sendEntity (entity) {
    },
    createNewEntity () {
      return {}
    },
    edit (entity) {
      let entityForEdit = {}
      if (entity) {
        Object.assign(entityForEdit, entity)
      } else {
        entityForEdit = this.createNewEntity()
      }
      this.entityForEdit = entityForEdit
      this.editModal = true
    },
    cancelEdit () {
      this.entityForEdit = null
      this.editModal = false
    },
    save () {
      this.$refs['editForm'].validate((valid) => {
        if (valid) {
          this.sendEntity(this.entityForEdit)
          this.cancelEdit()
        } else {
          this.$message({
            message: 'Введите корректно все необходимые данные',
            type: 'error'
          })
          return false
        }
      })
    },
    remove (entity) {
      this.$confirm('Удаление необратимо, Вы уверены что хотите удалить строку?', 'Внимание', {
        confirmButtonText: 'Да',
        cancelButtonText: 'Отмена',
        type: 'warning'
      }).then(() => {
        this.deleteEntity(entity)
          .then(() => {
            this.$message({
              type: 'success',
              message: 'Строка удалена'
            })
          },
          () => {
            this.$message({
              type: 'error',
              message: 'Ошибка удаления'
            })
          })
      })
    }
  }
}



