import Vue from 'vue'
import Router from 'vue-router'
import BookKeeping from '@/components/book-keeping.vue'
import Categories from '@/components/categories.vue'
import Report from '@/components/report.vue'
import Settings from '@/components/settings.vue'
import MoneySources from '@/components/money-sources.vue'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'book-keeping',
      component: BookKeeping
    },
    /* {
      path: '/categories',
      name: 'categories',
      component: Categories
    }, */
    {
      path: '/report',
      name: 'report',
      component: Report
    },
    {
      path: '/settings',
      name: 'settings',
      component: Settings,
      redirect: { name: 'categories' },
      children: [
        {
          path: '/settings/categories',
          name: 'categories',
          component: Categories
        },
        {
          path: '/settings/money-sources',
          name: 'moneySources',
          component: MoneySources
        }
      ]
    }
  ]
})



