// jshint ignore: start
import { mapGetters, mapActions } from 'vuex'

export default {
  created () {
    this.getCategories()
    this.getMoneySources()
  },
  computed: {
    ...mapGetters(['categories', 'categoriesByType', 'moneySources'])
  },
  methods: {
    ...mapActions(['getCategories', 'getMoneySources'])
  }
}



