<template lang="pug">
  div
    el-card(v-loading="loading")
      el-row
        el-col(:sm="12")
          el-select(v-model="transactionType")
            el-option(label="Расходы" :value="1")
            el-option(label="Доходы" :value="2")
      el-alert(style="margin-top: 20px" type="info" v-if="transactions.length === 0" title="Данные для отчета отсутствуют" show-icon :closable="false")
      div(v-if="transactions.length > 0")
        el-row
          el-col(:sm="15")
            chart(:options="chartData")
          el-col(:sm="9")
            h2 {{ total.toLocaleString() }} грн
            br
            template(v-for="totalBySource in totalBySources")
              h3.source-report {{ totalBySource.title }} : {{ totalBySource.amount}} грн
              hr

</template>

<script>
import { mapGetters } from 'vuex'
export default {
  name: 'report',
  data () {
    return {
      yearMonth: new Date(Date.now()),
      transactionType: 1,
      loading: false
    }
  },
  computed: {
    ...mapGetters([
      'incomes',
      'expenses',
      'categoriesByType',
      'category',
      'totalExpense',
      'totalIncome',
      'totalIncomeBySources',
      'totalExpenseBySources',
      'moneySource']),
    transactions () {
      return this.transactionType === 1 ? this.expenses : this.incomes
    },
    totalBySources () {
      return this.transactionType === 1 ? this.totalExpenseBySources : this.totalIncomeBySources
    },
    total () {
      return this.transactionType === 1 ? this.totalExpense : this.totalIncome
    },
    chartData () {
      var expenseCategories = this.categoriesByType[this.transactionType === 1 ? 0 : 1].categories.map((t) => t.title)
      var expenses = {}
      this.transactions.forEach(t => {
        var category = this.category(t.categoryId)
        if (category.type !== this.transactionType) {
          return
        }
        if (!expenses[category.title]) {
          expenses[category.title] = 0
        }
        expenses[category.title] += t.amount
      })
      var data = []
      for (var prop in expenses) {
        data.push({ name: prop, value: expenses[prop] })
      }
      return {
        title: {
          text: 'Отчет',
          x: 'center'
        },
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b} : {c} грн ({d}%)'
        },
        legend: {
          orient: 'vertical',
          data: expenseCategories
        },
        series: [{
          name: 'Расходы',
          type: 'pie',
          radius: ['50%', '70%'],
          data: data
        }]
      }
    }
  }
}
</script>

<style>
  .source-report {
    margin-top: 20px
  }
</style>



