<template lang="pug">
    div
        el-dialog(:visible.sync="editModal" width="60%" title="Категория")
          el-form(:inline="true"
            :model="entityForEdit"
            :rules="rules"
            v-if="editModal"
            ref="editForm"
            label-position="top")
            el-form-item(label="Тип" prop="type")
              el-select(v-model="entityForEdit.type")
                el-option(label="Расходы" :value="1")
                el-option(label="Доходы" :value="2")
            el-form-item(label="Название" prop="title")
              el-input(v-model="entityForEdit.title" placeholder="Введите название")
            el-form-item(label="Действия")
              el-button-group
                el-button(type="success" @click="save") Сохранить
                el-button(@click="cancelEdit") Отменить
        el-button(type="success" style="float: right" icon="el-icon-plus" @click="edit()") Добавить категорию
        div(v-for="(group, index) in categoriesByType" style="margin-top:20px" :key="index")
            h4 {{ group.title }}
            el-table(:data="group.categories")
                el-table-column(label="Название" prop="title")
                el-table-column
                    template(slot-scope="scope")
                      el-button-group
                        el-button(size="mini" @click="edit(scope.row)") Корректировать
                        el-button(type="danger" size="mini" @click="remove(scope.row)") Удалить
</template>

<script>
import { mapActions } from 'vuex'
import BookKeeping from '@/mixins/book-keeping'
import EntityListEdit from '@/mixins/entity-list-edit'
export default {
  name: 'categories',
  mixins: [BookKeeping, EntityListEdit],
  data () {
    return {
      entityForEdit: null,
      editModal: false,
      rules: {
        type: [{ type: 'number', required: true, message: 'Обязательно к заполнению', trigger: 'blur' }],
        title: [{ type: 'string', required: true, message: 'Обязательно к заполнению', trigger: 'blur' }]
      }
    }
  },
  methods: {
    ...mapActions([
      'createCategory',
      'updateCategory',
      'deleteCategory'
    ]),
    deleteEntity (entity) {
      return this.deleteCategory(entity)
    },
    sendEntity (entity) {
      entity.id ? this.updateCategory(entity) : this.createCategory(entity)
    },
    createNewEntity () {
      return {
        title: '',
        icon: null,
        type: 1
      }
    }
  }
}
</script>

<style>

</style>



