<template lang="pug">
  div
    el-tabs(type="border-card" @tab-click="changeRoute" :value="$route.name")
      el-tab-pane(label="Настройки категорий" name="categories")
        router-view
      el-tab-pane(label="Источники денег" name="moneySources")
        router-view
</template>

<script>
export default {
  name: 'settings',
  methods: {
    changeRoute (p) {
      this.$router.push({ name: p.name })
    }
  }
}
</script>

<style>

</style>



