
import axios from 'axios'
const isProduction = process.env.NODE_ENV === 'production'
var createInstance = (token, transformRequest) => {
  const axiosConfig = {
    baseURL: isProduction ? 'http://f3mann-001-site1.dtempurl.com/api' : 'http://localhost:49757/api'
  }
  if (token) {
    axiosConfig.headers = {
      Authorization: `Bearer ${token}`
    }
  }
  return axios.create(axiosConfig)
}
export default {
  get (url, { request, token }) {
    return this.request('get', url, { request, token })
  },
  post (url, { request, token }) {
    return this.request('post', url, { request, token })
  },
  patch (url, { request, token }) {
    return this.request('patch', url, { request, token })
  },
  delete (url, { request, token }) {
    return this.request('delete', url, { request, token })
  },
  request (type, url, { request, token }) {
    let axiosInstance = createInstance(token)
    return new Promise((resolve, reject) => {
      axiosInstance[type](url, request)
        .then(response => resolve(response))
        .catch(error => reject(error))
    })
  }
}



