import HTTP from '@/utils/http-common'

// endpoints
const URL_CREATE_TOKEN = '/auth/token'
const URL_GET_USER = '/auth/userinfo'

// mutation types
const LOGIN = 'LOGIN'
const LOGIN_SUCCESS = 'LOGIN_SUCCESS'
const LOGOUT = 'LOGOUT'
const WHO_I_AM = 'WHO_I_AM'
const TOKEN_KEY = 'USER_TOKEN'
const UNAUTHORIZED = 401

const state = {
  session: {
    logged: !!localStorage.getItem(TOKEN_KEY),
    token: localStorage.getItem(TOKEN_KEY),
    user: {
      name: ''
    }
  }
}
const getters = {
  logged: state => state.session.logged,
  token: state => state.session.token,
  userName: state => state.session.user.name
}

const mutations = {
  [LOGIN] (state) {
    state.pending = true
  },
  [LOGIN_SUCCESS] (state, { token }) {
    state.session.logged = true
    state.session.token = token
    localStorage.setItem(TOKEN_KEY, token)
  },
  [LOGOUT] (state) {
    localStorage.removeItem(TOKEN_KEY)
    state.session.token = null
    state.session.logged = false
  },
  [WHO_I_AM] (state, { name }) {
    state.session.user.name = name
  }
}

const actions = {
  login ({ state, commit, dispatch }, credentials) {
    var data = {}
    Object.assign(data, credentials)
    return new Promise((resolve, reject) => {
      HTTP.post(URL_CREATE_TOKEN, { request: data })
        .then(response => {
          commit(LOGIN_SUCCESS, { token: response.data.access_token })
          dispatch('whoIAm')
          resolve(true)
        })
        .catch(error => reject(error))
    })
  },
  logout ({state, commit}) {
    commit(LOGOUT)
  },
  whoIAm ({ state, commit, getters }) {
    return new Promise((resolve, reject) => {
      if (!getters.logged) {
        resolve()
        return
      }
      HTTP.post(URL_GET_USER, { token: state.session.token })
        .then(response => {
          resolve()
          commit(WHO_I_AM, { name: response.data })
        }, error => {
          reject(error)
          if (error.response.status === UNAUTHORIZED) {
            console.log(error.response)
            commit(LOGOUT)
          }
        })
    })
  }
}
export default {
  state,
  getters,
  actions,
  mutations
}



