<script>

import 'inputmask/dist/inputmask/inputmask.numeric.extensions.js'
import Inputmask from 'inputmask/dist/inputmask/inputmask.js'
import { Input } from 'element-ui'
export default {
  name: 'numericInput',
  mixins: [Input],
  props: {
    positive: {
      type: Boolean,
      default: true
    }
  },
  data () {
    return {
      muttableValue: this.value
    }
  },
  mounted () {
    var that = this
    Inputmask({
      groupSeparator: ' ',
      autoGroup: true,
      alias: 'numeric',
      rightAlign: false,
      removeMaskOnSubmit: true,
      allowMinus: true,
      allowPlus: false,
      autounmask: true,
      decimalProtect: false,
      radixPoint: '.',
      enforceDigitsOnBlur: true
    }).mask(this.$refs.input)
    var evHandler = function () {
      that.mutableValue = parseFloat(this.inputmask.unmaskedvalue())
      if (isNaN(that.mutableValue)) {
        that.mutableValue = null
      }
      that.$emit('input', that.mutableValue)
    }
    this.$refs.input.addEventListener('input', evHandler)
  }
}
</script>

<style>

</style>



