<template lang="pug">
  .book-keeping
    el-card
      el-row
        el-col(:sm="12") &nbsp;
        el-col(:sm="12")
          el-button(type="success" style="float: right" icon="el-icon-plus" @click="edit()") Добавить транзакцию
      el-dialog(:visible.sync="editModal" width="65%" title="Транзакция")
        el-form(:inline="true"
          :model="entityForEdit"
          v-if="editModal"
          :rules="rules"
          ref="editForm"
          label-position="top")
          el-form-item(label="Категория" prop="categoryId")
            el-select(v-model="entityForEdit.categoryId"
              filterable
              placeholder="Выбрать категорию")
              el-option-group(v-for="group in categoriesByType" :label="group.title" :key="group.title")
                el-option(v-for="category in group.categories"
                  :key="category.id"
                  :label="category.title"
                  :value="category.id")
          el-form-item(label="Дата" prop="date")
            el-date-picker(v-model="entityForEdit.date" placeholder="Выбрать дату" format="dd.MM.yyyy" value-format="dd.MM.yyyy")
          el-form-item(label="Сумма" prop="amount" style="width: 100px")
            //(:label="(entityForEdit.categoryId && category(entityForEdit.categoryId).type === 1) ? '-' : ''")
            numeric-input(v-model="entityForEdit.amount" placeholder="Ввести сумму")
          el-form-item(label="Источник")
            el-select(v-model="entityForEdit.moneySourceId")
              el-option(v-for="(moneySource, index) in moneySources" :key="index" :value="moneySource.id" :label="moneySource.title")
          el-form-item(label="Заметки" prop="note")
            el-input(v-model="entityForEdit.note" placeholder="Ввести заметку" :maxlength="50")
          el-button-group(style="margin-top: 10px; float: right;")
            el-button(type="success" @click="save") Сохранить
            el-button( @click="cancelEdit") Отменить
          div(style="clear: both")
      el-table(:data="transactions")
        el-table-column(label="Категория")
          template(slot-scope="scope")
            span {{ category(scope.row.categoryId).title }}
        el-table-column(prop="date" label="Дата")
        el-table-column(label="Сумма, грн")
          template(slot-scope="scope")
            span(:class="getAmountClass(scope.row)") {{ getAmountCaption(scope.row) }}
        el-table-column(label="Заметки" prop="note")
        el-table-column(label="Действия")
          template(slot-scope="scope")
            el-button-group
              el-button(type="primary" size="mini" @click="edit(scope.row)" icon="el-icon-edit")
              el-button(type="danger" size="mini" @click="remove(scope.row)" icon="el-icon-delete")
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import EntityListEdit from '@/mixins/entity-list-edit'
import NumericInput from '@/components/numeric-input.vue'
export default {
  name: 'book-keeping',
  mixins: [EntityListEdit],
  data () {
    return {
      rules: {
        categoryId: [{ type: 'number', required: true, message: 'Обязательно к заполнению', trigger: 'blur' }],
        amount: [{ type: 'number', required: true, message: 'Обязательно к заполнению', trigger: 'blur' }],
        date: [{ type: 'string', required: true, message: 'Обязательно к заполнению', trigger: 'blur' }]
      }
    }
  },
  components: {
    NumericInput
  },
  computed: {
    ...mapGetters([
      'transactions',
      'categories',
      'categoriesByType',
      'tags',
      'category',
      'moneySources'
    ])
  },
  methods: {
    ...mapActions([
      'createTransaction',
      'updateTransaction',
      'deleteTransaction'
    ]),
    createNewEntity () {
      return {
        categoryId: null,
        date: '',
        amount: null,
        note: null,
        moneySourceId: null
      }
    },
    deleteEntity (entity) {
      return this.deleteTransaction(entity)
    },
    sendEntity (entity) {
      var category = this.category(entity.categoryId)
      if (category.type === 1 && entity.amount > 0) {
        entity.amount = -entity.amount
      }
      if (category.type === 2 && entity.amount < 0) {
        entity.amount = -entity.amount
      }
      console.log(entity.amount)
      entity.id ? this.updateTransaction(entity) : this.createTransaction(entity)
    },
    getAmountCaption (row) {
      return row.amount.toLocaleString()
    },
    getAmountClass (row) {
      return row.amount <= 0
        ? 'negative-amount'
        : 'positive-amount'
    }
  }
}

</script>
<style lang="stylus">
  .header-column
    color #707070
    font-weight bold
    font-size 14px
  .action-group
    opacity 0.3
  .action-group:hover
      opacity 1
  .transaction-row
    padding 15px 0 15px 0
    border-bottom 1px solid #f5f5f5
</style>



